import React from 'react'
import "./About.css";

import MyPic from '../HomeLayout/images/rohit-pic.png'

export default function About() {
    return (
        <main>

            <section className="banner-section bg-image aboutBanner-section">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="banner-header fontFamily-BebasNeue">
                                <h1 className="banner-heading text-center text-white fw-bold">About</h1>
                            </div>
                        </div>
                    </div>
                </div>
            </section>


            <section className="section-padding about-section">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="section-header">
                                <h2 className="section-heading">About</h2>
                            </div>

                            <div className="sub-section-content my-md-5 my-3">
                                <div className="sub-section-body">
                                    <div className="sub-section-body">
                                        <img className="img-fluid" src={MyPic} alt="rohit..." />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </main>
    )
}
