import React, {useEffect} from 'react'
import './HomeLayout.css'

import Aos from 'aos'
import "aos/dist/aos.css"

import MyPic from './images/rohit-pic.png'
import MyPic2 from './images/rohit-pic4.jpg'

import { Link } from "react-router-dom"


function HomeLayout() {
    useEffect(() => {
        Aos.init({duration:2000});
    }, [])


    return (
        <React.Fragment>
            <section className="banner-section bg-image home-section">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="banner-header fontFamily-BebasNeue">
                                <h1 className="banner-heading text-center text-white fw-bold">Home Page</h1>
                            </div>
                        </div>
                    </div>
                </div>
            </section>


            <section className="section-padding homeAbout-section overflowX-hidden">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="section-content">
                                <div className="section-header">
                                    <h2 className="section-heading">Introduction</h2>
                                </div>


                                <div className="sub-section-content my-md-5 my-3">
                                    <div className="sub-section-header">
                                        <h3 className="sub-section-heading">About Me</h3>
                                    </div>
                                    <div className="sub-section-body">
                                        <p className="section-text">Hi, my name is Rohit. I am a front-end developer,
                                            currently working as a <strong>Web Developer Trainee</strong> at <strong>Velocity Consultancy</strong> for
                                            three months.
                                            Now I'm looking for job in <strong>React Development</strong>. I'm learning ReactJs for six months and have
                                            good knowledge of ReactJs packages like Axios, Bootstrap, Aos.....
                                            <Link to="/project" className="fw-bold"> See Projects</Link> 
                                        </p>
                                        <p className="section-text">At <strong>Velocity Consultancy</strong>,
                                            I develop live websites for clients by using Html, CSS, Bootstrap, WordPress, and Php.
                                        </p>
                                    </div>
                                </div>
                                
                                <div className="sub-section-content">
                                    <div className="sub-section-header">
                                        <h3 className="sub-section-heading">Experience</h3>
                                    </div>
                                    <div className="sub-section-body">
                                        <p className="section-text" data-aos="fade-left">I have a total of two years of experience.
                                            Worked as a Sports Analyst at Hudl Sports India for one year and as a Broadcast Engineer
                                            at PlanetCast Media Services and Limited for one year.
                                        </p>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </section>


            <section className="section-bottom-padding homeCarousel-section overflowX-hidden">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="section-content">
                                <div className="section-header">
                                    <h2 className="section-heading">Bootstrap Carousel</h2>
                                </div>

                                <div className="sub-section-content mb-md-5 mb-3">
                                    <div className="sub-section-body">
                                        <p className="section-text" data-aos="fade-left"></p>
                                        <div className="row">
                                            <div className="col-md-6">
                                                <div className="carousel slider" id="hltestimonial" data-bs-ride="carousel">
                                                    <div className="carousel-indicators">
                                                        <button type="button" className="active" data-bs-target="#hltestimonial" data-bs-slide-to="0"
                                                            aria-label="slide 1"
                                                            aria-current="true"></button>
                                                        <button type="button" data-bs-target="#hltestimonial" data-bs-slide-to="1" aria-label="slide 2"></button>
                                                        <button type="button" data-bs-target="#hltestimonial" data-bs-slide-to="2" aria-label="slide 3"></button>
                                                    </div>
                                                    <div className="carousel-inner">
                                                        <div className="carousel-item active">
                                                            <div className="citem-parent p-3 citem-parent-text1">
                                                                <img src={MyPic} alt="..." />
                                                                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit.
                                                                    Nobis non numquam officiis suscipit facere rem.</p>
                                                            </div>
                                                        </div>
                                                        <div className="carousel-item">
                                                            <div className="citem-parent p-3 citem-parent-text2">
                                                                <img src="" alt="..." />
                                                                <p>This is a awesome website.</p>
                                                            </div>
                                                        </div>
                                                        <div className="carousel-item">
                                                            <div className="citem-parent p-3 citem-parent-text3">
                                                                <img src={MyPic2} alt="..." />
                                                                <p>I love this website.</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <button type="button" class="carousel-control-prev" data-bs-target="#hltestimonial"
                                                        data-bs-slide="prev">
                                                        <span class="carousel-control-prev-icon"></span>
                                                    </button>
                                                    <button type="button" class="carousel-control-next" data-bs-target="#hltestimonial"
                                                        data-bs-slide="next">
                                                        <span class="carousel-control-next-icon"></span>
                                                    </button>
                                                </div>

                                            </div>
                                        </div>
                                        
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </div>
            </section>


        </React.Fragment>
    )
}

export default HomeLayout
