import React, { useState, useEffect } from "react"
import "./SearchBar.css"

const SearchBar = () => {
    const [Cars, setCars] = useState([])
    useEffect(() => {
        setTimeout(() => {
            setCars([
                "BMW",
                "Lamborghini",
                "Ferrari",
                "Kia",
                "Maruti"
            ])

        }, 2000)

    }, [])

    const products = [
        "tooth paste",
        "tooth brush",
        "tounge cleaner",
        "mouth guard",
        "mouth wash"
    ]
    const Fruits = [
        "mango",
        "apple",
        "banana",
        "grapes",
        "papaya"
    ]

    return (
        <React.Fragment>
            <div className="section-content">
                <div className="section-header mb-md-5 mb-3">
                    <h2 className="section-heading">Project-5</h2>
                </div>


                <div className="sub-section-content mb-md-5 mb-3">
                    <div className="sub-section-header">
                        <h3 className="sub-section-heading">Filtered search bar using React hooks</h3>
                    </div>                        
                    <div className="sub-section-body">
                        <div className="sub-section-text-wrapper">
                            <DuplicateSearchBar products={products} />
                        </div>
                        <div className="sub-section-text-wrapper">
                            <DuplicateSearchBar products={Fruits} />
                        </div>
                    </div>
                </div>

                <div className="sub-section-content">
                    <div className="sub-section-header">
                        <h3 className="sub-section-heading">Filtered search bar with setTimeout function</h3>
                    </div>                        
                    <div className="sub-section-body">
                        <div className="sub-section-text-wrapper">
                            <DuplicateSearchBar products={Cars} />
                        </div>
                    </div>
                </div>
                
            </div>
        </React.Fragment>
    )
}
export default SearchBar


// ---Duplicate Search Bar ---

const DuplicateSearchBar = (props) => {
    const [inputValue, setinputValue] = useState("")


    const InputChangeFunc = (event) => {
        setinputValue(event.target.value)
    }
    const buttonClickFunc = () => {
        setinputValue("")
    }

    const filterProducts = props.products.filter((product) => {
        return product.includes(inputValue)
    })

    const inputLength = inputValue.length > 0

    return (
        <React.Fragment>
            <input type="text" value={inputValue} onChange={InputChangeFunc} />
            {inputLength && <button type="button" onClick={buttonClickFunc}>Clear</button>}
            {inputValue}
            <ul>
                {filterProducts.map((product) => {
                    return <li key={product}>{product}</li>
                })}
            </ul>
        </React.Fragment>
    )
}