import React, { useState, useEffect, Component } from "react"
import "./CountButton.css"

const CountButton = () => {

    return (
        <React.Fragment>
            <div className="section-content">
                <div className="section-header mb-md-5 mb-3">
                    <h2 className="section-heading">Project-4</h2>
                </div>


                <div className="sub-section-content">
                    <div className="sub-section-header">
                        <h3 className="sub-section-heading">Count Buttons Using Props</h3>
                    </div>                        
                    <div className="sub-section-footer">
                        <div className="section-footer-wrapper mb-3">
                            <DuplicateCountButton incrementBy={1} />
                        </div>
                        <div className="section-footer-wrapper">
                            <DuplicateCountButton incrementBy={4} />
                        </div>
                    </div>
                </div>
                
            </div>
        </React.Fragment>
    )
}
export default CountButton


// Another count button Component

const DuplicateCountButton = (props) => {
    const [currentcount, setCurrentCount] = useState(0);
    function CountBtn() {
        setCurrentCount(
            currentcount + props.incrementBy
        )
    }
    useEffect(() => {
        if (currentcount == 10) {
            setCurrentCount(0)
        }
    }, [currentcount])
    return (
        <React.Fragment>
            <button type="button" className="btn btn-primary d-inline-block me-3" onClick={CountBtn}>+{props.incrementBy}</button>
            <p className="d-inline-block"><code>{currentcount}</code></p>
        </React.Fragment>
    )
}