import React from 'react'
import "./Contact.css"

export default function Contact() {
    return (
        <main>

            <section className="banner-section bg-image contact-section">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="banner-header fontFamily-BebasNeue">
                                <h1 className="banner-heading text-center text-white fw-bold">Contact</h1>
                            </div>
                        </div>
                    </div>
                </div>
            </section>


            <section className="section-padding">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="section-content">
                                <div className="section-header">
                                    <h2 className="section-heading">Contact</h2>
                                </div>

                                <div className="sub-section-content my-md-5 my-3">
                                    <div className="sub-section-body">
                                        <form action="">
                                            <div className="row">
                                                <div className="col-md-6 pb-4">
                                                    <label htmlFor="" className="form-label">Email Id</label>
                                                    <input type="text" name="" id="" placeholder="Enter Your Name" className="form-control" />
                                                    <div className="form-text">This input is private</div>
                                                </div>
                                                <div className="col-md-6 pb-4">
                                                    <label htmlFor="" className="form-label">Your Name</label>
                                                    <input type="email" name="" id="" placeholder="Enter Your Email" className="form-control" />
                                                    <div className="form-text">This input is private</div>
                                                </div>
                                                <div className="col-md-6 pb-4">
                                                    <label htmlFor="" className="form-label">Subject</label>
                                                    <input type="text" name="" id="" placeholder="Enter Your Subject" className="form-control" />
                                                    <div className="form-text">This input is private</div>
                                                </div>
                                                <div className="col-12 pb-4">
                                                    <textarea name="" className='mb-3 form-control' placeholder="Message" id="" cols="30" rows="6"></textarea>
                                                </div>
                                                <div className="col-12">
                                                    <button className="btn btn-success">Send Message</button>
                                                </div>
                                            </div>
                                        </form>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>


            <section>
                <div className="container">
                    <div className="row">
                        <div className="col">
                        </div>
                    </div>
                </div>
            </section>


        </main>
    )
}



