import React, { useState } from "react"
import "./Practice.css"


const Practice = (props) => {
    const [count, setCount] = useState(props.incrementBy)
    const [theme, setTheme] = useState("red")
    const [textValue, setTextValue] = useState("")

    const incrementCount = () => {
        setCount(count + props.incrementBy)
        setTheme("blue")
    }
    const textInput = (event) => {
        setTextValue(event.target.value)
    }
    const clearBtn = () => {
        setTextValue("")
    }
    const shuoldDisplayButton = textValue.length > 0

    

    return (
        <React.Fragment>
            <div className="section-content">
                <div className="section-header mb-md-5 mb-3">
                    <h2 className="section-heading">PRACTICE</h2>
                </div>


                <div className="sub-section-content mb-md-5 mb-3">
                    <div className="sub-section-header">
                        <h3 className="sub-section-heading">Variable</h3>
                    </div>                        
                    <div className="sub-section-body">
                        <div className="sub-section-text-wrapper">
                            <VarPractice />
                        </div>
                    </div>
                </div>

                <div className="sub-section-content mb-md-5 mb-3">   
                    <div className="sub-section-header">
                        <h3 className="sub-section-heading">Math</h3>
                    </div>                        
                    <div className="sub-section-body">
                        <div className="sub-section-text-wrapper">
                            <Mathpractice />
                        </div>
                    </div>
                </div>

                <div className="sub-section-content mb-md-5 mb-3">   
                    <div className="sub-section-header">
                        <h3 className="sub-section-heading">IfElse</h3>
                    </div>                        
                    <div className="sub-section-body">
                        <div className="sub-section-text-wrapper">
                            <IfElsePractice />
                        </div>
                    </div>
                </div> 

                <div className="sub-section-content mb-md-5 mb-3">   
                    <div className="sub-section-header">
                        <h3 className="sub-section-heading">Use State</h3>
                    </div>                        
                    <div className="sub-section-body">
                        <div className="sub-section-text-wrapper">
                            <UseStatePractice />
                        </div>
                    </div>
                </div>
                
            </div>

            <input type="text" value={textValue} placeholder="Type here" onChange={textInput} />{textValue}<br />
            {shuoldDisplayButton && <button type="button" onClick={clearBtn}>X</button>}
            <br />
            <button type="button" onClick={incrementCount}>+</button>
            <span>{count}</span>
            <span>{theme}</span>
        </React.Fragment>
    )
}
export default Practice

const VarPractice = () => {
    const myvar = "practice";
    const fruits = ["mangos", "orange", "strawberry", 12, true];
    fruits[0] = "grapes";
    fruits.push("Variable, array, and object");
    const Cars = {
        name: "BMW",
        color: "black",
        model: "unknown ",
        launch: 2012,
        fullName: function () {
            return this.name + " " + this.color
        }
    }
    return (
        <React.Fragment>
            <p>this paragraph is for {myvar} purpose, In this we are practicing {fruits[5]} !</p>
            {fruits.length}
            <br />
            {Cars.model}
            {Cars.fullName()}
        </React.Fragment>
    )
}

const Mathpractice = () => {
    const numbers = [1, 2, 3, 4, 5, 6, 7]
    return (
        <React.Fragment>
            {numbers.map((number) => {
                return <div key = {number}>
                    {number * 3}
                </div>
            })}
        </React.Fragment>
    )
}
const IfElsePractice = () => {
    const isLoggedIn = false
    const Names = ["rohit", "yadav", "alex", "penelope", 3]
    return (
        <React.Fragment>
            {isLoggedIn && "welcome rohit"}
            {<p>I welcome {isLoggedIn ? " this" : "that"}</p>}
            { Names.map((myname, i, array) => {
                return <div key={myname} >{myname.length}</div>
            })}
            {Names.filter((Name) => {
                return Name
            })}y
        </React.Fragment>)
}
const UseStatePractice = () => {

    // const state = React.useState(98)
    // const count = state[0]
    // const setCount = state[1]
    //           OR
    const [count, increment] = useCount()
    return (
        <button onClick={() => increment()}>You have clicked {count} times</button>
    )
}
function useCount() {
    const [count, setCount] = useState(0)
    function increment() {
        setCount(count => count + 1)
    }
    return [count, increment]
}

