import React from "react"
import "./Navbar.css"


import { Link, NavLink } from "react-router-dom"

const Navbar = () => {
    return (
        <React.Fragment>
            <div className="navbar navbar-dark bg-dark navbar-expand-lg sticky-top">
                <div className="container">
                    <Link to="/reactjs-project" className="navbar-brand fw-bold link-color">Devro</Link>
                    <button
                        className="navbar-toggler"
                        type="button"
                        data-bs-toggle="collapse"
                        data-bs-target="#devroNavbar">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="devroNavbar">
                        <ul className="nav navbar-nav me-auto">
                            <li className="nav-item"><NavLink to="/reactjs-project" activeClassName="isLinkActive" exact className="nav-link link-color">Home</NavLink></li>
                            <li className="nav-item"><NavLink to="/about" activeClassName="isLinkActive" className="nav-link link-color">About</NavLink></li>
                            <li className="nav-item"><NavLink to="/project" activeClassName="isLinkActive" className="nav-link link-color">Projects</NavLink></li>
                            <li className="nav-item"><NavLink to="/contact" activeClassName="isLinkActive" className="nav-link link-color">Contact</NavLink></li>
                            <li className="dropdown nav-item">
                                <a
                                    role="button"
                                    href="#"
                                    className="dropdown-toggle nav-link link-color"
                                    type="button"
                                    id="navbardropdownbtn1"
                                    data-bs-toggle="dropdown"
                                    area-aria-haspopup="true">
                                    Dropdown
                                </a>
                                <ul className="dropdown-menu" area-aria-labelledby="navbardropdownbtn1">
                                    <li> <a href="#" className="dropdown-item">link1</a></li>
                                    <li> <a href="#" className="dropdown-item">link2</a></li>
                                    <li> <a href="#" className="dropdown-item">link3</a></li>
                                </ul>
                            </li>
                        </ul>
                        <form action="" className="d-flex">
                            <input type="text" className="form-control me-2" />
                            <button className="btn btn-success">Search</button>
                        </form>
                    </div>
                </div>
            </div>






            <header>
                <div className="container">
                    <div className="row">
                        <div className="col">

                        </div>
                    </div>
                </div>
            </header>
        </React.Fragment>
    )
}


export default Navbar