import React, { useState, useEffect } from "react"
import "./FetchingApi.css"

const FetchingApi = () => {
    const [fakeApiValue, setfakeApiValue] = useState([])

    useEffect(() => {
        fetch('https://fakestoreapi.com/products')
            .then(res => res.json())
            .then((productsArray) => {
                const mappedproductsArray = productsArray.map((product) => {
                    return product.title
                })
                setfakeApiValue(mappedproductsArray)
            })
    }, [])

    const hasproducts = fakeApiValue.length > 0

    return (
        <React.Fragment>
            <div className="section-content">
                <div className="section-header mb-md-5 mb-3">
                    <h2 className="section-heading">Project-2</h2>
                </div>


                <div className="sub-section-content">
                    <div className="sub-section-header">
                        <h3 className="sub-section-heading">Fetch Api in React using React hooks</h3>
                    </div>                        
                    <div className="sub-section-body">
                        <div className="sub-section-text-wrapper">
                            {hasproducts ? <SearchBar products={fakeApiValue} /> : "Loading..."}
                            {/* ternary conditional operator */}
                        </div>
                    </div>
                </div>
                
            </div>
        </React.Fragment>
        )
}
export default FetchingApi





// I used same code that i already used for SearchBar 

const SearchBar = (props) => {

    const [inputValue, setinputValue] = useState("")

    const InputChangeFunc = (event) => {
        setinputValue(event.target.value)
    }
    const buttonClickFunc = () => {
        setinputValue("")
    }
    const inputLength = inputValue.length > 0

    const filterProducts = props.products.filter((product) => {
        return product.includes(inputValue)
    })

    return (
        <React.Fragment>
            <input type="text" value={inputValue} onChange={InputChangeFunc} />
            {inputLength && <button type="button" onClick={buttonClickFunc}>Clear</button>}<br /> 
            {/* short circuiting */}
            {inputValue}
            <ul>
                {filterProducts.map((product) => {
                    return <li key={product}>{product}</li>
                })}
            </ul>

        </React.Fragment>
    )
}