import React, { useState, useEffect } from 'react'
import "./Project.css"
import Aos from 'aos'
import "aos/dist/aos.css"


import CountButton from './CountButton/CountButton'
import SearchBar from './SearchBar/SearchBar'
import FetchingApi from './FetchingApi/FetchingApi'
import Practice from './Practice/Practice'
import TodoList from './TodoList/TodoList'

const Project = () => {
    const [items, setitems] = useState([]);
    const [newitems, setnewitems] = useState([]);
    useEffect(() => {
        fetchItems()
    }, [])

    useEffect(() => {
        getCovidData()
    }, [])
    useEffect(() => {
        Aos.init({duration:2000});
    }, [])


    const fetchItems = async () => {
        const data = await fetch("https://api.covid19api.com/summary")
        const items = await data.json()

        console.log(items)

        setitems(items.Global)
    }
    
    const getCovidData = async () => {
        const data = await fetch("https://api.covid19api.com/summary")
        const items = await data.json()

        console.log(items.Countries)
        console.log(items.Countries[76])

        setnewitems(items.Countries[76])
    }

    return (
        <main>

            <section className="banner-section bg-image project-section">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="banner-header fontFamily-BebasNeue">
                                <h1 className="banner-heading text-center text-white fw-bold">Project</h1>
                            </div>
                        </div>
                    </div>
                </div>
            </section>


            <section className="section-padding corona-section overflowX-hidden">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="section-content">
                                <div className="section-header mb-md-5 mb-3">
                                    <h2 className="section-heading">Project-1</h2>
                                </div>


                                <div className="sub-section-content mb-md-5 mb-3">
                                    <div className="sub-section-header">
                                        <h3 className="sub-section-heading">Live Corona Updates Globally</h3>
                                    </div>
                                    <div className="sub-section-body">
                                        <p className="sub-section-text">As we know, Almost for two years the coronavirus is made our life difficult in so many different ways so,
                                            I decided to make a coronavirus tracker which can detect live coronavirus cases globally and country-wise.
                                        </p>
                                        <p className="sub-section-text">
                                            In this project, I am using the fetch() method to get the data from 'https://api.covid19api.com/summary' 
                                            this API and by using react hooks like useState, useEffect rendering the data on a browser.
                                        </p>
                                        <div className="sub-section-text-wrapper">
                                            <div className="row">
                                                <div className="col-md-6">
                                                    <div className="corona-card" data-aos="slide-left">
                                                        <h4 className="corona-heading">Last Update From</h4>
                                                        <h4 className="corona-result"><code>{items.Date}</code></h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card" data-aos="slide-right">
                                                        <h4 className="corona-heading">New Confirmed corona cases</h4>
                                                        <h4 className="corona-result"><code>{items.NewConfirmed} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card" data-aos="slide-left">
                                                        <h4 className="corona-heading">New Recovered corona cases</h4>
                                                        <h4 className="corona-result"><code>{items.NewRecovered} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card" data-aos="slide-right">
                                                        <h4 className="corona-heading">New Deaths corona cases</h4>
                                                        <h4 className="corona-result"><code>{items.NewDeaths} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card" data-aos="slide-left">
                                                        <h4 className="corona-heading">Total Confirmed cases</h4>
                                                        <h4 className="corona-result"><code>{items.TotalConfirmed} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card" data-aos="slide-right">
                                                        <h4 className="corona-heading">Total Recovered corona cases</h4>
                                                        <h4 className="corona-result"><code>{items.TotalRecovered} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card" data-aos="slide-left">
                                                        <h4 className="corona-heading">Total Deaths corona cases</h4>
                                                        <h4 className="corona-result"><code>{items.TotalDeaths} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                            </div>

                                        </div>
                                    </div>
                                </div>

                                <div className="sub-section-content">
                                    <div className="sub-section-header">
                                        <h3 className="sub-section-heading">Live Corona Updates In India</h3>
                                    </div>
                                    <div className="sub-section-body">
                                        <p className="sub-section-text">A little over a month ago, India recorded its highest-ever surge of 4.14 lakh Covid cases in a single day.
                                            That's when the devastating second wave of Covid had finally peaked in India.
                                        </p>
                                        <p className="sub-section-text">Over the last month, the daily new infections have maintained
                                            a steady declining trend, here you can see the live corona cases in india.
                                        </p>
                                        <div className="sub-section-text-wrapper">
                                            <div className="row">
                                                <div className="col-md-6">
                                                    <div className="corona-card corona-card-india" data-aos="flip-right">
                                                        <h4 className="corona-heading">Last Update From</h4>
                                                        <h4 className="corona-result"><code> {newitems.Date} </code></h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card corona-card-india" data-aos="flip-left">
                                                        <h4 className="corona-heading">Country</h4>
                                                        <h4 className="corona-result"><code>{newitems.Country} </code></h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card corona-card-india" data-aos="flip-right">
                                                        <h4 className="corona-heading">New Confirmed corona cases</h4>
                                                        <h4 className="corona-result"><code>{newitems.NewConfirmed} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card corona-card-india" data-aos="flip-left">
                                                        <h4 className="corona-heading">New Recovered corona cases</h4>
                                                        <h4 className="corona-result"><code>{newitems.NewRecovered} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card corona-card-india" data-aos="flip-right">
                                                        <h4 className="corona-heading">New Deaths corona cases</h4>
                                                        <h4 className="corona-result"><code>{newitems.NewDeaths} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card corona-card-india" data-aos="flip-left">
                                                        <h4 className="corona-heading">Total Confirmed corona cases</h4>
                                                        <h4 className="corona-result"><code>{newitems.TotalConfirmed} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card corona-card-india" data-aos="flip-right">
                                                        <h4 className="corona-heading">Total Recovered corona cases</h4>
                                                        <h4 className="corona-result"><code>{newitems.TotalRecovered} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="corona-card corona-card-india" data-aos="flip-left">
                                                        <h4 className="corona-heading">Total Deaths corona cases</h4>
                                                        <h4 className="corona-result"><code>{newitems.TotalDeaths} </code><span className="dot"> </span> Live</h4>
                                                    </div>
                                                </div>
                                            </div>
                                            
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>



            <section className="section-padding border-top border-4 border-secondary" id="fetchApiSection">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <FetchingApi />
                        </div>
                    </div>
                </div>
            </section>



            <section className="section-padding border-top border-4 border-secondary">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <TodoList />
                        </div>
                    </div>
                </div>
            </section>



            <section className="section-padding border-top border-4 border-secondary">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <CountButton />
                        </div>
                    </div>
                </div>
            </section>



            <section className="section-padding border-top border-4 border-secondary">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <SearchBar />
                        </div>
                    </div>
                </div>
            </section>



            <section className="section-padding border-top border-4 border-secondary">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <Practice />
                        </div>
                    </div>
                </div>
            </section>

        </main>
    )
}
export default Project;




